export default "/* Global variables */\n/* Colors */\n/* Fonts */\n/* Transitions */\n/* Border box */\n@media (max-width: 992px) {\n  .navbar-collapse {\n    position: absolute;\n    top: 120px;\n    left: 0;\n    right: 0;\n    padding: 40px 0px;\n    background-color: #fff;\n    box-shadow: 0 3px 3px rgba(182, 182, 182, 0.75);\n  }\n  .navbar-collapse .nav-link {\n    margin: 20px;\n    display: block;\n    margin-bottom: 20px;\n  }\n  .navbar-collapse .primary-button {\n    display: none;\n  }\n}\n.navbar {\n  position: absolute;\n  width: 100%;\n  padding: 30px;\n  display: flex;\n  height: 120px;\n  z-index: 2;\n}\n.navbar .navbar-brand img {\n  width: 85px;\n}\n.navbar .navbar-collapse {\n  justify-content: flex-end;\n}\n.navbar .nav-link {\n  transition: all 0.3s ease 0s;\n  color: rgba(0, 0, 0, 0.8);\n  padding: 0px 15px;\n  text-transform: uppercase;\n  letter-spacing: 2px;\n  font-size: 14px;\n  font-weight: 400;\n}\n.navbar .nav-link:hover {\n  transition: all 0.3s ease 0s;\n  color: black;\n}\n.navbar .nav-link::after {\n  content: \"\";\n  display: block;\n  width: 0;\n  height: 2px;\n  background: #000;\n  transition: width 0.3s;\n  margin-top: 5px;\n}\n.navbar .nav-link:hover::after {\n  width: 100%;\n}\n.navbar .active .nav-link::after {\n  color: black;\n  width: 100%;\n}\n.navbar .navbar-toggler {\n  outline: none;\n  border: 0px;\n  color: rgba(0, 0, 0, 0.8);\n}\n.navbar .navbar-toggler:hover {\n  color: black;\n}\n/*# sourceMappingURL=data:application/json;base64, */"